import Swal from "sweetalert2";
export const validatephone = (e) => {
  const value = e;
  console.log(value, "value");
  const reg = /^1([0-9]*)?$/; // 以数字1开头，任意数字结尾，且中间出现零个或多个数字
  if (reg.test(value) && 1 <= value.length <= 11) {
    return true;
  } else {
    return false;
  }
};
export const showDialog = (msg, type, functions) => {
  Swal.fire({
    text: msg,
    icon: type,
    confirmButtonText: localStorage.getItem('language') === 'zh-cn' ? '确定' : 'sure',
    customClass: {
      icon: type === 'error' ? "del-icon" : 'suc-icon',
    },
    //  "success",
    // reverseButtons: true // 翻转确定，取消
  }).then((isConfirm) => {
    try {
      //判断 是否 点击的 确定按钮
      if (isConfirm.value) {
        functions(isConfirm.value);
        // Swal.fire("成功", "点击了确定", "success");
      } else {
        functions(isConfirm.value);
        // Swal.fire("取消", "点击了取消", "error");
      }
    } catch (e) {
      return "error";
    }
  });
};
// 防抖
export const debounce = (fn, ms) => {  //fn:要防抖的函数 ms:时间
  let timerId
  return function () {
    timerId && clearTimeout(timerId)
  
    timerId = setTimeout(() => {
      fn.apply(this, arguments)
    }, ms)
  }
}
/**
* 生成密码字符串
* 33~47：!~/
* 48~57：0~9
* 58~64：:~@
* 65~90：A~Z
* 91~96：[~`
* 97~122：a~z
* 123~127：{~
* @param length 长度  生成的长度是length + 2
* @param hasNum 是否包含数字 1-包含 0-不包含
* @param hasChar 是否包含字母 1-包含 0-不包含
* @param hasSymbol 是否包含其他符号 1-包含 0-不包含
* @param caseSense 是否大小写敏感 1-敏感 0-不敏感
* @param lowerCase 是否只需要小写，只有当hasChar为0且caseSense为1时起作用 1-全部小写 0-全部大写
*/
export function genEnCode (length, hasNum, hasChar, hasSymbol, caseSense, lowerCase) {
  var m = ''
  if (hasNum === 0 && hasChar === 0 && hasSymbol === 0) return m
  for (var i = length; i >= 0; i--) {
    var num = Math.floor((Math.random() * 94) + 33)
    if (
      (
        (hasNum === 0) && ((num >= 48) && (num <= 57))
      ) || (
        (hasChar === 0) && ((
          (num >= 65) && (num <= 90)
        ) || (
            (num >= 97) && (num <= 122)
          ))
      ) || (
        (hasSymbol === 0) && ((
          (num >= 33) && (num <= 47)
        ) || (
            (num >= 58) && (num <= 64)
          ) || (
            (num >= 91) && (num <= 96)
          ) || (
            (num >= 123) && (num <= 127)
          ))
      )
    ) {
      i++
      continue
    }
    m += String.fromCharCode(num)
  }
  if (caseSense === '0') {
    m = (lowerCase === '0') ? m.toUpperCase() : m.toLowerCase()
  }
  return m
}
